.wrapper {
    background: #111419;
    border-right: 1px solid rgba(0, 0, 0, .07);
    box-shadow: 0 0 15px rgb(0 0 0 / 7%);
    height: 100%;
    left: 0;
    overflow: hidden;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 260px;
    z-index: 2;
    /*box-sizing: border-box;*/
}

@media (max-width: 1000px) {
    .wrapper {
        width: 100%;
        height: 66px;
    }

    .container {
        flex-direction: row!important;
        justify-content: center;
    }

    .photoContainer {
        padding: 0!important;
    }

    .photoContainer img {
        display: none;
    }
    .photoContainer h1 {
        font-size: 1.3125rem;
        margin: 0;
    }

    .navContainer {
        display: none;
    }
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-wrap: inherit;

    height: 100%;
    padding: 16px;
    color: #fff;
    box-sizing: border-box;
    position: relative;
}

.photoContainer {
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;

}

.photo {
    height: 150px;
    width: 150px;
    border: 10px solid #4c4d4d;
    border-radius: 100%;
    object-fit: cover;
}

.navContainer {
    /*height: 100%;*/
    width: 100%;
    font-size: 20px;
    text-align: left;
}

.navContainer ul {
    list-style: none;
}

.navContainer li {
    padding: 10px;
}

.active {
    color: #20c997;
}

